<template>
  <v-row>
    <v-col class="py-0 px-4">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              Пользователи
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['All', 'Admin', 'Moderator', 'User']"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
      </v-card>

      <ApolloQuery :query="require('../graphql/users.gql')">
        <template v-slot="{ result: { data, error }, isLoading }">
          <!-- Loading -->
          <div v-if="isLoading" class="loading apollo">Loading...</div>

          <!-- Error -->
          <div v-else-if="error" class="error apollo">An error occurred</div>

          <!-- Result -->
          <div v-else-if="data" class="result apollo">
            <v-data-table
              :headers="headers"
              :items="data.getUsers.users"
              :page.sync="page"
              :items-per-page="itemsPerPage"
              hide-default-footer
              @page-count="pageCount = $event"
            > 
              <template v-slot:item.name="props">
                <div class="my-1">
                  <v-avatar 
                    size="35" 
                    class="mr-2"
                  >
                    <img :src="props.item.img">
                  </v-avatar>
                  <span>{{ props.item.name }}</span>
                </div>
              </template>
              <template v-slot:item.createdAt="props">
                {{ props.item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
              </template>
              <template v-slot:item.updatedAt="props">
                {{ props.item.updatedAt | moment('DD.MM.YYYY, HH:mm') }}
              </template>
              <template v-slot:item.action="props">
                <v-btn 
                  :to="{ name: 'users', params: { id: props.item.id }}" 
                  icon
                >
                  <v-icon small>
                    mdi-pencil
                  </v-icon>
                </v-btn>
              </template> 
            </v-data-table>
            <div class="text-center pt-2">
              <v-pagination v-model="page" :length="pageCount"></v-pagination>
              <v-text-field
                :value="itemsPerPage"
                label="Пользователей на странице"
                type="number"
                min="1"
                max="100"
                @input="itemsPerPage = parseInt($event, 10)"
              ></v-text-field>
            </div>
          </div>

          <!-- No result -->
          <div v-else class="no-result apollo">No result</div>
        </template>
      </ApolloQuery>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    headers: [
      { text: 'ID', align: 'left', value: 'id' },
      { text: 'Имя', value: 'name' },
      { text: 'Custom ID', value: 'customId' },
      { text: 'Почта', value: 'email' },
      { text: 'Статус', value: 'status' },
      { text: 'Провайдер', value: 'provider' },
      { text: 'Роль', value: 'role' },
      { text: 'Зарегистирован', value: 'createdAt' },
      { text: 'Обновлен', value: 'updatedAt' },
      { text: 'Баланс', value: 'balance' },
      { text: 'Действия', value: 'action', sortable: false }
    ]
  })
}
</script>
